/* global jQuery */
void (function initializeModal($) {
	'use strict';

	function toggleModal(wrapper, toggle) {
		const $wrapper = $(wrapper);
		const $toggle = $(toggle);
		const animationSpeed = 400;

		$toggle.attr(
			'aria-expanded',
			$toggle.attr('aria-expanded') === 'true' ? false : true
		);
		$wrapper.attr(
			'aria-hidden',
			$wrapper.attr('aria-hidden') === 'true' ? false : true
		);

		// reinit custom scrolls that may be inside the modal, changing the height of it
		setTimeout(() => {
			$wrapper.find('[data-function="scroll"]').trigger('reinit');
		}, animationSpeed);
	}

	$.fn.makeModal = function makeModal() {
		return this.each((index, wrapper) => {
			const $wrapper = $(wrapper);
			const $toggle = $(`[data-toggle="${$wrapper.attr('id')}"]`);

			$('#Body').append($wrapper);

			$wrapper.click((e) => {
				e.preventDefault();

				toggleModal($wrapper, $toggle);
			});

			$wrapper
				.find('aside')
				.first()
				.click((e) => {
					e.stopPropagation();
				});

			$toggle.click((e) => {
				e.preventDefault();

				toggleModal($wrapper, $toggle);
			});

			$wrapper.trigger('modal-initialized');
		});
	};

	void (function init() {
		$('[data-function="modal"]').makeModal();
	})();
})(jQuery);
